.about {width: 100%;float: left; padding:8.6rem 0 9rem 0;}
.about .wrapper { max-width: 1080px; }
.about h2 { width: 100%; float: left; text-align: left; line-height: 75px; letter-spacing: -1px; font-size: 56px; color: #fff; font-weight:600; }
.aboutcontainer { width: 100%; float: left; margin: 1rem 0 0 0; display: flex; align-items:flex-start; justify-content:space-between;  flex-direction: row-reverse;  }
.aboutcontainer img { width: 41%; float: left; border: 0;   }
.aboutcontainer p { margin-top: 1.5rem; width: 47%; float: left; text-align: left; line-height:24px; font-size: 16px; color: #fff; font-weight:400; }




@media only screen and (max-width: 1023px) 
{
 
    .about h2 { line-height:38px; letter-spacing:normal; font-size:32px; font-weight:400; }
    .about h2 span { font-weight:600; }
    .aboutcontainer {  margin: 1.5rem 0 0 0;   }
    .aboutcontainer img { width: 45%;  }
    .aboutcontainer p { width: 52%;  line-height:20px; font-size: 14px; }
    
    
}

@media only screen and (max-width: 767px) 
{
 
    .about h2 { text-align: center; }

    .aboutcontainer { align-items: flex-start;justify-content: center; flex-direction: inherit; flex-wrap: wrap;   }
    .aboutcontainer img { width: 100%; max-width: 258px;  }
    .aboutcontainer p { margin-top: 1rem; width: 100%;  text-align: center; }
    
    
}

@media only screen and (max-width: 479px) 
{
 
    .about { padding: 6rem 0 8.4rem 0; }
    .aboutcontainer { margin: 2.5rem 0 0 0; }
    .aboutcontainer p { margin-top: 2.3rem; }
}