.footer { background-color:#1A364D;  width: 100%;float: left;padding:6.2rem 0; }
.footer .wrapper { max-width: 1080px; }

.footer-info { width: 25%; float: left; }
.footer-info aside { width: 100%; float: left; }
.footer-info aside img { width: 78px; float: left; border: 0; }
.footer-info h2 { width: 100%;float: left; margin:1.6rem 0 1.6rem 0; text-align: left; line-height: 16px;  font-size: 12px; font-weight: 400; color: #AEAEAE; }
.footer-info ul {  width: 100%; float: left;  text-align: left; display: flex; align-items:center; justify-content:flex-start; }
.footer-info ul li { list-style: none; margin: 0 1rem 0 0;  }
.footer-info ul li a { text-decoration: none; background: #294660; border-radius: 50%; width: 28px; height: 28px; padding:6px; display: flex; justify-content: center; align-items: center; transition: all 0.6s ease-out; }
.footer-info ul li a:hover { background: #000; transition: all 0.6s ease-in;  }
.footer-info ul li a img {  display: inline-block; max-width: 100%; width: 22px; border: 0; margin: 0;  }
.footer-info p {  width: 100%;float: left; margin:1rem 0 0 0; text-align: left; line-height: 18px;  font-size: 12px; font-weight: 400; color: #AEAEAE;  }

.footer-link { width: 52.5%; float:right; }
.footer-link ul { width: 50%; float: left; }
.footer-link ul li { list-style: none; width: 100%;float: left; margin:0.1rem 0 0.7rem 0; }
.footer-link ul li h2 { width: 100%;float: left; margin:0 0 0.5rem 0; text-align: left;  font-weight: 700; font-size: 16px; line-height: 20px; letter-spacing: -0.318182px; color: #FFFFFF; }
.footer-link ul li a { text-decoration: none; font-weight: 400; font-size: 18px; line-height: 26px;letter-spacing: -0.5px;color: #FFFFFF;  transition: all 0.6s ease-out; }
.footer-link ul li a:hover { text-decoration: underline; transition: all 0.6s ease-in;  }  


@media only screen and (max-width: 1023px) 
{
    .footer { padding:2rem 0;}
    
    .footer-info { width: 40%;  }
 
    .footer-info aside img { width: 70px;  }
    .footer-info h2 { margin:0.5rem 0 1rem 0; line-height: 16px;  font-size: 12px;  }
    .footer-info ul li { margin: 0 1rem 0 0;  }
    .footer-info p { margin:0.5rem 0 0 0;  line-height: 14px;  font-size: 12px;  }
    
    .footer-link { width: 55%; }
    .footer-link ul li {  margin:0 0 0.5rem 0; }
    .footer-link ul li h2 { margin:0 0 0.5rem 0; font-size: 16px; line-height: 24px; letter-spacing: 0.5px; }
    .footer-link ul li a { font-size: 12px; line-height: 18px;letter-spacing: normal;  }  
    
}

@media only screen and (max-width: 479px) 
{
    .footer {padding: 2.5rem 0; }
    .footer-info { width: 100%;  }
    .footer-info aside { text-align: center;}
    .footer-info aside img { display: inline-block; margin: 0 auto; float: none; }
    .footer-info h2 { margin:1.7rem 0 0 0; text-align: center; }
    .footer-info ul { justify-content: center; }
    .footer-info ul li { margin: 0 0.5rem; }
    .footer-info p {  margin:0.8rem 0 0 0; text-align: center; }


    .footer-link { width: 100%; margin:2.5rem 0;  }
    .footer-link ul { padding-left: 9%; }
    .footer-link ul li h2 { margin: 0 0 1rem 0; }
    
}