 .header {position: relative; background:#284A6D url("../images/bg-top.png") no-repeat center center; background-size: cover; width: 100%;float: left;padding:2rem 0 165px 0;} 
.header .wrapper { max-width: 1080px; }
.header:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 165px;
    background: linear-gradient(180deg, rgba(35, 71, 101, 0) 0%, #294660 90%);
}

.headtop { width: 100%;float: left;padding:1rem 0 5rem 0;display: flex;align-items:center; justify-content: space-between; }
.headtop img { width:auto; max-width: 100%; float: left; border: 0; }
.headtop aside {  width:auto; float: left;}
.headtop aside a { margin: 0 3rem; line-height: 20px; font-size: 16px; color: #fff; font-weight:400; text-decoration: none; transition: all 0.6s ease-out; }
.headtop aside a:hover { text-decoration: underline; color:#fff;   transition: all 0.6s ease-in; }
.headtop h3 {  width:auto; float: left;}
.headtop h3 a { background: transparent; border: 2px solid #A1A1A1; border-radius: 1rem; display: inline-block; padding: 1rem 2rem; line-height: 20px; font-size: 16px; color: #fff; font-weight:900; text-decoration: none; transition: all 0.6s ease-out; }
.headtop h3 a:hover { background: #fff; color:#284A6D; border-color: #284A6D;  transition: all 0.6s ease-in; }

.headbanner { width: 100%;float: left; display: flex; align-items:center; justify-content: space-between; flex-direction: row-reverse; }
.head-img  { width: 45%; float: left;  }
.headcontainer { width: 50%; float: left;  }
.headcontainer h2 { width: 100%; float: left; text-align: left; line-height: 94px; letter-spacing:1px; font-size: 78px; color: #fff; font-weight:600; }
.headcontainer p { width: 100%; float: left; margin: 2.5rem 0; text-align: left; line-height:27px; font-size: 20px; color: #fff; font-weight:400; }
.headcontainer h3 {  width: 100%; float: left; text-align: left;  }
.headcontainer h3 a { background: #fff; border: none; border-radius: 1rem; display: inline-block; padding: 1rem 3rem; line-height: 30px; font-size: 24px; color: #284A6D; font-weight:700; text-decoration: none; transition: all 0.6s ease-out; }
.headcontainer h3 a:hover { background: #234765; color:#fff;  transition: all 0.6s ease-in; }
.headcontainer ul {  width: 100%; float: left; margin-top: 2.5rem; text-align: left; display: flex; align-items:center; justify-content:flex-start; }
.headcontainer ul li { list-style: none; margin: 0 1.4rem 0 0;  }
.headcontainer ul li a { text-decoration: none;  }
.headcontainer ul li a img { transform: scale(1); display: inline-block; width: 21px; border: 0; margin: 0; transition: all 0.6s ease-out; }
.headcontainer ul li a img:hover { transform: scale(1.2); transition: all 0.6s ease-in; }



@media only screen and (max-width: 1023px) 
{
    .header { padding:1.5rem 0 100px 0; }
    .header:after { height: 100px; }
    .headtop { padding: 0 0 3rem 0; }
    .headtop aside a { margin: 0 1rem; font-size: 16px;  }
    .headtop h3 a {  border-width:1px; border-radius: 0.5rem; padding:0.7rem 1.5rem; line-height: 20px; font-size: 16px;  }
    
    .headcontainer h2 { line-height:58px;  font-size: 48px;  }
    .headcontainer p {  margin:1.5rem 0;  line-height:24px; font-size: 16px;  }
    .headcontainer h3 a { border-radius:1rem; padding:1rem 2.5rem; line-height: 20px; font-size: 16px;  font-weight:900;  }
    .headcontainer ul { margin-top: 3rem; }
    .headcontainer ul li { margin: 0 1rem 0 0;  }
    .headcontainer ul li a img {  width: 21px;  }
 
}



@media only screen and (max-width: 767px) 
{
    .headbanner { align-items:flex-start; justify-content:center; flex-direction: inherit;flex-wrap: wrap;  }
    .head-img  { width: 100%; max-width:250px; float: left;  }
    .headcontainer { margin-top: 1.7rem; width: 100%; float: left;  }
    .headcontainer h2 { text-align: center;  }
    .headcontainer p {  text-align: center;  }
    .headcontainer h3 {  text-align: center;  }
    .headcontainer ul { justify-content: center; }
    .headcontainer ul li { margin: 0 0.8rem ;  }
 
}

@media only screen and (max-width: 479px) 
{
 
    .header { padding:2rem 0 113px 0; }
    .header:after { height: 113px; }
    .headtop { padding: 0 0 4rem 0; justify-content: center; }
    .headtop img {  max-width: 72px;  }
    .headtop aside { display: none;}
    .headtop h3 { display: none;}

    .headcontainer h2 { letter-spacing: normal; }
}