
.newsupdate {  width: 100%;float: left; padding:9rem 0 13rem 0; }
.newsupdate .wrapper { max-width: 698px;}

.newsupdate h2 { width: 100%;float: left; margin: 0; text-align: center; line-height: 63px; letter-spacing: -1px; font-size: 54px; color: #fff; font-weight:400; letter-spacing: -1px; }
.newsupdate h2 span { font-weight:600;  }
.newsupdate p { width: 100%;float: left; padding: 0 15%;  margin:0.7rem 0 2.4rem 0; text-align: center; line-height: 26px;  font-size: 20px; color: #fff; font-weight:400; }

.newsupdate-form { width: 100%;float: left; display: flex; justify-content: space-between; align-items: center; }
.newsupdate-input {  width: 80%; height: auto; float:left; margin:0 1.5rem 0 0; padding:1rem 1.5rem;  text-align: left; background: #1A364D; border: 1px solid rgba(227, 204, 194, 0.5); border-radius: 1rem; outline: none;  line-height:1; font-size: 20px; color: #E3CCC2; font-weight: 600; } 
.newsupdate-input:-webkit-input-placeholder {  opacity: 1; font-size:80%; font-weight: 500; color:#E3CCC2; }
.newsupdate-input:-ms-input-placeholder {  opacity: 1; font-size:80%; font-weight: 500; color:#E3CCC2; }
.newsupdate-input::placeholder {  opacity: 1; font-size:80%; font-weight: 500; color:#E3CCC2;  }
.newsupdate-submit { width: 140px; height: auto; float:left; background: #fff; border:none; border-radius: 1rem; padding: 1rem 0; line-height:1; font-size: 24px; color: #284A6D; font-weight:700; text-decoration: none; cursor: pointer; text-align: center; transition: all 0.6s ease-out;  }
.newsupdate-submit:hover { background: #284A6D; color:#fff; transition: all 0.6s ease-in; }
.newsupdate-error {  width: 100%; float: left;  margin:0.5rem 0 0 0; padding:0; box-sizing: border-box; text-align: left; font-size: 14px; line-height: 1; color: #f30; font-weight: 400;  }
.newsupdate-success{ width: 100%; float: left;  margin:1rem 0 0 0; padding:0; box-sizing: border-box; text-align: center; font-size: 18px; line-height: 1.3; color: #fff; font-weight: 700; }
.newsupdate-success b { font-size: 24px; font-weight: 900; }



@media only screen and (max-width: 1023px) 
{

 
 
    .newsupdate h2 { line-height: 38px; letter-spacing: normal; font-size: 32px;   }
    .newsupdate p { padding: 0 10%; margin:0.8rem 0 2rem 0; line-height: 18px; font-size: 14px;  }
    .newsupdate-form { justify-content: center; }
    .newsupdate-input {  width:280px; margin:0 1rem 0 0; padding:0.9rem 1rem; border-radius:0.7rem; font-size: 20px;   } 
    .newsupdate-submit { width: 120px; border-radius: 0.7rem; padding: 0.9rem 0; font-size: 16px;    }
 
    
    
}


@media only screen and (max-width: 479px) 
{
    .newsupdate {  padding:6.5rem 0 7.7rem 0; }
    .newsupdate p { margin: 1rem 0 2.5rem 0; }

 
    .newsupdate-form { width: 88%; margin: 0 6%;  flex-direction: column; }
    .newsupdate-input { width: 100%; margin:0 0 1.6rem 0;   } 
    .newsupdate-submit { width: 160px; padding: 1rem 0; font-weight:600; }
 
    .newsupdate-error { text-align: center; margin: 1rem 0 0 0;  }
    
}