
.members { width: 100%; float:left; }
.members .wrapper { max-width: 1080px; }
.members h2 { width: 100%; float: left; margin: 0 0 2.5rem 0; text-align: center; line-height: 75px; letter-spacing: -1px; font-size: 56px; color: #fff; font-weight:600; }
.members ul { width: 100%; float: left; display: flex; align-items:stretch; justify-content:space-between; flex-wrap: wrap; }
.members ul li { list-style: none; width:31.3%; margin: 1rem 0; padding: 1.6rem 1.4rem 2.5rem 1.4rem; background: #234765; border: 1px solid rgba(227, 204, 194, 0.6); border-radius: 1.5rem; }
.members ul li aside { width: 100%; float: left; text-align: left; background: #fff; border-radius: 1rem; padding:1rem 1.25rem; }
.members ul li aside img { display: inline-block; max-width: 100%; border: 0;   }
.members ul li p { width: 100%; float: left; margin: 1.7rem 0 0 0; text-align: left; line-height:24px; font-size: 16px; color: #fff; font-weight:400; }



@media only screen and (max-width: 1023px) 
{
    .members h2 {  margin: 0 0 1.5rem 0; line-height: 37px; letter-spacing:normal; font-size: 32px; font-weight:400; }
    .members h2 span {   font-weight:600; }
    .members ul li { width:32.3%; margin: 1rem 0; padding:1rem; border-radius:1rem; }
    .members ul li aside { text-align: center; border-radius: 1rem; padding:0.5rem 1rem; }
    .members ul li p { text-align: center;  margin: 1rem 0 0 0; line-height:20px; font-size: 14px; }
    
    
}

@media only screen and (max-width: 599px) 
{
    .members h2 {  margin: 0 0 1.3rem 0;  }
    .members ul { align-items:center; justify-content:center; flex-direction: column; }
    .members ul li { width:100%; max-width: 257px; margin: 0.5rem 0; }
    .members ul li aside { padding: 0.3rem 1rem; }
    
}

 