
.container { width: 100%; float: left; }
.container .wrapper { max-width: 1080px; }

.hdarow { width: 100%; float: left; padding:4.1rem 0; display: flex; align-items:center; justify-content:space-between;  }
 
.hdacolleft {  width: 52%; float: left; padding-right: 1%;  }
.hdacolleft h2 { display: none; width: 100%; float: left; margin:0 0 2rem 0; text-align: center; line-height:52px; font-size: 40px; color: #fff; font-weight:600; letter-spacing: -1px; }
.hdacolleft aside { width: 100%; float: left; text-align: center; }
.hdacolleft aside img { display: inline-block; width: 100%; max-width: 280px; border: 0;   }

.hdacolright {  width: 48%; float: right;  }
.hdacolright h2 { width: 100%; float: left; margin:0; text-align: left; line-height:48px; font-size: 40px; color: #fff; font-weight:600; letter-spacing: -1px; }
.hdacolright p { width: 100%; float: left; margin:0.8rem 0 1.5rem 0; text-align: left; line-height:36px; font-size:24px; color: #fff; font-weight:400; }
.hdacolright h3 {  width: 100%; float: left; text-align: left;  }
.hdacolright h3 a { background: #fff; border: none; border-radius: 1rem; display: inline-block; padding: 1rem 2rem; line-height: 30px; font-size: 24px; color: #284A6D; font-weight:700; text-decoration: none; transition: all 0.6s ease-out; }
.hdacolright h3 a:hover { background: #234765; color:#fff;  transition: all 0.6s ease-in; }


@media only screen and (max-width: 1023px) 
{
 
    

    .hdacolleft { width: 40%; }
    .hdacolleft h2 { line-height:30px; font-size: 24px;   letter-spacing:normal; }
    .hdacolleft aside img { max-width: 150px;  }
    
    .hdacolright { width: 55%; }
    .hdacolright h2 { line-height:30px; font-size: 24px;  letter-spacing:normal;  }
    .hdacolright p {  margin:0.8rem 0 1.5rem 0; line-height:24px; font-size:16px;  }
    .hdacolright h3 a {  border-radius: 0.7rem;  padding:0.7rem 1.5rem; line-height: 20px; font-size: 16px;   }
      
}


@media only screen and (max-width: 479px) 
{
    .hdarow { padding:3rem 0 5rem 0;  justify-content:center; flex-wrap: wrap; }
 
    .hdacolleft { width: 100%; }
    .hdacolleft h2 { display: block; }
    .hdacolleft aside { text-align: center; }
    .hdacolleft aside img { max-width: 150px;   }
 
    .hdacolright { width: 100%; padding: 0 5%; }
    .hdacolright h2 { display: none;    }
    .hdacolright p { margin:2.5rem 0 1.5rem 0; text-align: center;  }
    .hdacolright h3 {  text-align: center;  }
 
 
}