.headtop h3 {  width:auto; float: left;}
.headtop h3 a { background: transparent; border: 2px solid #A1A1A1; border-radius: 1rem; display: inline-block; padding: 1rem 2rem; line-height: 20px; font-size: 16px; color: #fff; font-weight:900; text-decoration: none; transition: all 0.6s ease-out; }
.headtop h3 a:hover { background: #fff; color:#284A6D; border-color: #284A6D;  transition: all 0.6s ease-in; }

