    .footer-info aside img { display: inline-block; margin: 0 auto; float: none; }
    .footer-info h2 { margin:1.7rem 0 0 0; text-align: center; }
    .footer-info ul { justify-content: center; }
    .footer-info ul li { margin: 0 0.5rem; }
    .footer-info p {  margin:0.8rem 0 0 0; text-align: center; }


    .footer-link { width: 100%; margin:2.5rem 0;  }
    .footer-link ul { padding-left: 9%; }
    .footer-link ul li h2 { margin: 0 0 1rem 0; }
    
}

