
.workgroups {  width: 100%;float: left; }
.workgroups .wrapper { max-width: 854px;}

.workgroups h2 { width: 100%; float: left; margin: 0 0 2rem 0; text-align: center; line-height: 48px; letter-spacing: -1px; font-size: 40px; color: #fff;  font-weight:600; }
.workgroups p {  width: 100%; float: left;  margin: 0 0 0.5rem 0;  padding:0 1.5rem; display: flex; align-items:center; justify-content:space-between;  }
.workgroups p span {  line-height:20px; font-size:16px; color: #fff;  font-weight:400; }
.workgroups ul {  width: 100%; float: left;   }
.workgroups ul li { width: 100%; float: left; margin:0.5rem 0; display: flex; align-items:center; justify-content:space-between; padding: 1.5rem; border: 1px solid rgba(227, 204, 194, 0.4);
    border-radius: 1rem;  }
.workgroups ul li h3 { width: auto; float: left; text-align: left;  line-height:30px; font-size:24px; color: #E3CCC2; font-weight:700; }
.workgroups ul li h4 {  width: auto; float: right; display: flex; align-items:center; justify-content:flex-end; }
.workgroups ul li h4 img { width: 22px; float: left; margin-right: 5px; padding:1px; border: 0; background: #fff; border-radius: 50%; }
.workgroups ul li h4 span { width: auto; float: left;  line-height:24px; font-size:16px; color: #fff;  font-weight:500;  }




@media only screen and (max-width: 1023px) 
{

    .workgroups h2 { margin: 0 0 2rem 0; line-height: 38px; letter-spacing: 1.5px; font-size: 32px;  }
    .workgroups p { margin: 0 0 0.5rem 0;  padding:0 1rem;  }
    .workgroups p span {  line-height:18px; font-size:12px;  }
    .workgroups ul li { margin:0.5rem 0; padding: 1rem;  border-radius:0.7rem;  }
    .workgroups ul li h3 { width: 63%; line-height:20px; font-size:16px;  }
    .workgroups ul li h4 { width: 36%; }
    .workgroups ul li h4 img { width: 18px; }
    .workgroups ul li h4 span { line-height:14px; font-size:12px;   }
    
    
}

@media only screen and (max-width: 479px) 
{
    .workgroups p { width: 82%; margin:1rem 9% 0.5rem 9%; padding: 0; }
    .workgroups p span {line-height: 16px;font-size: 10px;}
    .workgroups ul { width: 86%;margin: 0 7%; }

    .workgroups ul li { margin: 0.3rem 0; }
    .workgroups ul li h4 span { font-size: 10px; }

}