@font-face {
  font-family: "Gilroy";
  src: url("assets/fonts/Gilroy-Regular.woff2") format("woff2"), url("assets/fonts/Gilroy-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
}

body {
  margin:0;
  padding:0;
  font-family: -apple-system, 'Gilroy', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  font-weight: 400;
}
 
.pageloader {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 8;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #294660;
  text-align: center;
}
.pageloader div {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.pageloader img 
{
  width: 72px; margin: 0 auto; display: inline-block; border: 0;
}


.App {
  display:block;
  float: left;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #294660;
  font-family: 'Gilroy', Arial, sans-serif;
}

.wrapper {
  display: flow-root;
  width: 100%;
  margin: 0 auto;
  float: none;
}




@media only screen and (max-width: 1023px) 
{
  .wrapper { width: 94%; }

}

@media only screen and (max-width:479px) 
{
 
  .wrapper { width: 95%; }

}