.latestnews { width: 100%; float: left; padding:12rem 0 11rem 0; }
.latestnews .wrapper { max-width: 862px; }

.latestnews h2 { width: 100%; float: left; margin: 0 0 1.5rem 0; text-align: center; line-height: 48px; letter-spacing: -1px; font-size:40px; color: #fff;  font-weight:600; }

.lnrow {  width: 100%; float: left; display: flex; align-items:flex-start; justify-content:space-between; flex-wrap: wrap; }
.lnrow .lncol { width: 48.7%; float: left; background: #234765; border: 1px solid #587084; padding: 1rem; margin: 1rem 0; border-radius: 1rem; }
.lnrow .lncol .lnin { width: 100%; float: left; display: flex; align-items:stretch; justify-content:space-between; }
.lnin aside { width: 120px; height: 120px; float: left; background: #E3CCC2; border-radius: 16px;  display: flex; align-items:center; justify-content:flex-start; }
.lnin aside img { display: inline-block; max-width: 100%; margin: 0 auto;  }
.lnin h5{ width: 20px; float: left; }
.lnin h3 { width:calc(100% - 140px); padding: 0.5rem 0 0 0;  float: left; display: flex; flex-flow: wrap; text-align: left;  line-height:30px; font-size:24px; color: #E3CCC2;  font-weight:700; }
.lnin h3 span { width: 100%; display: block; font-weight:600; color: #E3CCC2; font-size: 12px; line-height: 16px; }
.lnrow .lncol p { width: 100%; float: left; margin:1.4rem 0; text-align: left; line-height:24px; font-size:16px; color: #fff; font-weight:400; }
.lnrow .lncol h4 {  width:auto; clear: both; float: left; text-align: left;  }
.lnrow .lncol h4 a {  text-decoration: none; display: flex; justify-content: flex-start; align-items: center;  }
.lnrow .lncol h4 a span { width: auto; float: left;  line-height:20px; font-size: 16px; color: #ffffff; font-weight:900; text-decoration: none;  transition: all 0.5s ease-out; border-bottom: solid 1px transparent;  }
.lnrow .lncol h4 a:hover span { border-bottom-color:#fff; transition: all 0.5s ease-in; }
.lnrow .lncol h4 a img { float: left; width: 11px; border: 0; margin:2px 0 0 5px; transition: all 0.5s ease-out; }
.lnrow .lncol h4 a:hover img {  margin-left:10px; transition: all 0.5s ease-in; }

.lnshowmore {  width:100%; margin-top:1.5rem; float: left; text-align: center;}
.lnshowmore .lnshowBtn { display: inline-block; padding: 1rem 2rem; margin: 0 auto; background: #234765; border:1px solid #E3CCC2; border-radius: 1rem; line-height: 25px; font-size: 20px; color: #E3CCC2; font-weight:900; cursor: pointer; text-decoration: none; transition: all 0.6s ease-out; }
.lnshowmore .lnshowBtn:hover { background: #fff; color:#284A6D; border-color: #284A6D;  transition: all 0.6s ease-in; }


@media only screen and (max-width: 1023px) 
{
 
 
    .latestnews h2 {  margin: 0 0 1rem 0; line-height: 38px; letter-spacing: normal; font-size:32px;  }
    
    .lnrow .lncol { width: 49%; margin: 0.5rem 0; padding: 1rem; border-radius: 1rem; }
    .lnin aside { width: 80px; height: 80px;   }
    .lnin h5{ width: 15px;   }
    .lnin h3 { width:calc(100% - 95px); padding: 0.5rem 0 0 0;  line-height:22px; font-size:18px;  }
    .lnin h3 span {  font-size: 10px; line-height: 14px; }
    .lnrow .lncol p {  margin:1rem 0;  line-height:20px; font-size:14px;  }
    .lnrow .lncol h4 a span { line-height:18px; font-size: 14px;   }
 
    .lnshowmore { margin-top:1rem; }
    .lnshowmore .lnshowBtn {  padding:0.7rem 1.5rem;  border-radius:0.7rem; line-height: 20px; font-size: 16px;  }
    
}


@media only screen and (max-width: 599px) 
{
    .lnrow .lncol { width: 100%; margin: 0.5rem 0; }
}


@media only screen and (max-width: 479px) 
{
    .latestnews { padding: 5.2rem 0 8.3rem 0; }
    .latestnews h2 { margin: 0 0 2rem 0; }
    .lnrow { justify-content: center; }
    .lnrow .lncol { width: 88%; margin: 0.7rem 0; }
    .lnshowmore .lnshowBtn { padding: 0.9rem 2.2rem; }
}